/* Global styles for all components */

/* --- General Page Styles --- */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1, h2, h3 {
  margin: 0;
  padding: 0.5em 0;
  font-weight: normal;
}

button {
  padding: 0.5em 1em;
  background-color: #28a745;
  color: white;
  border: none;
  cursor: pointer;
}

button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}

img {
  max-width: 100%;
  height: auto;
}

/* --- Landing Page Styles --- */
.landing-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: url(images/paradise-nursery.jpg) center/cover no-repeat;
  color: white;
  text-align: center;
}

.landing-page h1 {
  font-size: 3em;
}

.landing-page p {
  font-size: 1.5em;
  margin-bottom: 1em;
}

.landing-page button {
  font-size: 1.2em;
}

/* --- Header Styles --- */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #4CAF50;
  padding: 1em;
  color: white;
}

.logo {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.logo p{
  text-align: center;
}

.logo img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin-right: 10px;
}

.logo h1 {
  margin: 0;
  font-size: 1.5em;
  color: white;
}

.cart-icon {
  position: relative;
  cursor: pointer;
}

.cart-icon .cart-count {
  position: absolute;
  top: -10px;
  right: -10px;
  background-color: red;
  color: white;
  border-radius: 50%;
  padding: 2px 6px;
  font-size: 0.9em;
}

/* --- Product Listing Page Styles --- */
.product-listing {
  padding: 2em;
}

.plant-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5em;
}

.plant-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 1em;
  width: 200px;
  text-align: center;
}

.plant-card img {
  height: 250px;
  object-fit: cover;
}

.plant-card button {
  margin-top: 1em;
  width: 100%;
}

.product-listing-page h2 {
  text-align: center;
  margin-top: 20px; /* Optional: Adjust the spacing as needed */
  padding: 10px;
}

/* --- Plant Card Styles --- */
.plant-card {
  border: 1px solid #ddd;
  margin-left: 20px;
  border-radius: 8px;
  padding: 1em;
  width: 240px;
  text-align: center;
  background-color: #cdc7c7;
}

.plant-card img {
  height: 200px;
  width: 200%;
  object-fit: cover;
}

.plant-card h3 {
  color: #2f855a; /* Example color for the plant name */
}

.plant-card p.description {
  color: black; /* Example color for the description */
  font-size: 1rem;
  margin: 0.5em 0;
}

.plant-card p.price {
  color: #0a0a0a; /* Example color for the price */
  font-size: 1.2rem;
  font-weight: bold;
  margin: 0.5em 0;
}

.plant-card button {
  margin-top: 1em;
  width: 100%;
}

/* --- General Page Styles --- */
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  height: 100%;
  background-color: #f0f8f5;
  box-sizing: border-box;
}

/* --- Header Styles --- */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #4CAF50;
  padding: 1em;
  color: white;
  margin: 0; /* Ensure no margin from the top */
}

.logo {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.logo img {
  width: 40px;
  margin-right: 10px;
}

.logo h1 {
  margin: 0;
  font-size: 1.5em;
}

.cart-icon {
  position: relative;
  cursor: pointer;
}

.cart-icon .cart-count {
  position: absolute;
  top: -10px;
  right: -10px;
  background-color: red;
  color: white;
  border-radius: 50%;
  padding: 2px 6px;
  font-size: 0.9em;
}

/* --- Shopping Cart Page Styles --- */
.shopping-cart {
  padding: 4em;
}

.cart-items {
  display: flex;
  flex-direction: column;
  gap: 1.5em;
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  border: 1px solid #ddd;
  border-radius: 8px;
}

.cart-item img {
  width: 200px;
  height: 200px;
  object-fit: cover;
}

.cart-item div {
  flex: 1;
  margin-left: 1em;
}

.cart-item h3 {
  color: #2f855a; /* Example color for the item name */
}

.cart-item p {
  margin: 0.5em 0;
}

.cart-item p.price {
  color: black; /* Example color for the price */
  font-size: 1.2rem;
  font-weight: bold;
}

.cart-item p.subtotal {
  color: #4a5568; /* Example color for the subtotal */
  font-size: 1rem;
}

.cart-item button {
  margin: 5px;
  padding: 7px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cart-item button:hover {
  background-color: #276749;
}

.cart-item button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(47, 133, 90, 0.4);
}

.cart-summary {
  margin-top: 2em;
  text-align: right;
}

.cart-summary h2 {
  margin-bottom: 0.5em;
}

.cart-summary button {
  background-color: #28a745; /* Green background for both buttons */
  color: white;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  margin: 10px;
  cursor: pointer;
  margin-right: 2px;
  transition: background-color 0.3s ease;
}
