/* LandingPage.css */

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  height: 100%;
  background-color: #f0f8f5;
}

.landing-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
  position: relative;
  background-color: #f0f8f5;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('../images/paradise-nursery.jpg') center/cover no-repeat;
  z-index: -1;
 
}

h1 {
  font-size: 5rem;
  color: #2f855a;
  margin-bottom: 20px;
  font-weight: bold;
}

p {
  font-size: 1.5rem;
  color: white; 
  margin-bottom: 40px;
  max-width: 600px;
  line-height: 1.6;
}


button {
  padding: 15px 30px;
  font-size: 1.25rem;
  color: white;
  background-color: #2f855a;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #276749;
}

button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(47, 133, 90, 0.4);
}
